.activity-item {
    display: flex;
    justify-content: space-between;
    align-items: center;
    box-shadow: 0 2px 8px rgba(26, 25, 25, 0.25);
    padding: 0.5rem;
    margin: 1rem 0;
    border-radius: 12px;
    background-color:#ada397;
  }
  
  .activity-item__description {
    display: flex;
    flex-direction: column;
    gap: 1rem;
    align-items: flex-end;
    flex-flow: column-reverse;
    justify-content: flex-start;
    flex: 1;
  }
  
  .activity-item h2 {
    color: #3a3a3a;
    font-size: 1rem;
    flex: 1;
    margin: 0 1rem;
    color: white;
  }
  
  .activity-item__time {
    font-size: 1rem;
    font-weight: bold;
    color: white;
    background-color: #eca1a6;    ;
    border: 1px solid white;
    padding: 0.5rem;
    border-radius: 12px;
  }
  
  @media (min-width: 580px) {
    .activity-item__description {
      flex-direction: row;
      align-items: center;
      justify-content: flex-start;
      flex: 1;
    }
  
    .activity-item__description h2 {
      font-size: 1.25rem;
    }
  
    .activity-item__price {
      font-size: 1.25rem;
      padding: 0.5rem 1.5rem;
    }
  }